.btn.custom-bg:hover,
.btn.custom-bg:active {
  background-color: var(--accent-color);
  border-color: transparent;
  color: #ffffff;
}

.quiz-image {
  height: 185px;
  overflow: hidden;
}

.quiz-image img {
  width: 100%;
  height: 100%;
  object-position: 50% 50%;
  object-fit: cover;
}

.quiz-chapter {
  font-size: 2em;
}

.quiz-title {
  font-size: 1.3em;
  font-weight: bold
}

.included-topics {
  list-style-type: square;
  padding: 10px;
  margin-left: 5%;
  font-size: 1.1em;
  font-weight: 400;
  margin-bottom: 30px;
}

.included-topics li {
  padding: 5px 7px;
}
