.task {
  width: 80%;
  margin: 15px auto;
  font-size: 16px;
}

.question-wrapper {
  margin-bottom: 15px;
}

.question {
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 1.4em;
  letter-spacing: 0;
  color: #000000;
}

.question .code-example {
  max-width: 600px;
}

.code-example {
  padding: 10px 15px;
  font-size: 1.3em;
  color: rgb(42, 39, 36);
  font-weight: 400;
  font-style: italic;
  margin-left: 20px;
  border: 1px solid rgba(168, 158, 144, 0.07);
  background-color: rgba(168, 158, 144, 0.03);
  user-select: none;
}

.variants-list {
  list-style: none;
  padding-left: 0;
  font-size: 1.1em;
}

.variant {
  padding: 15px 5px 15px 15px;
  border-bottom: 1px solid #f2f2f2;
  cursor: pointer;
  transition: all 0.4s ease;
  font-weight: 400;
}

.variant:not(.selected):hover {
  background-color: rgba(199, 168, 118, 0.1);
}

.selected {
  background-color: rgba(199, 168, 118, 0.5);
}

@media (max-width: 576px) {
  .task {
    width: 100%;
    font-size: 14px;
  }
}
